a { 
  color: #fff; 
  text-decoration: none; 
  outline: 0;
  transition:0.5s all ease; 
}

a:hover,
a:focus,
a:active,
a.active {
  text-decoration: none;
  outline: 0;
  color: #9a1212 !important;
  transition:0.5s all ease;
}


