.kafe-btn{
  display: inline-block;
  margin-bottom: 0;
  font-weight: normal;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  line-height: 1.4;
  cursor: pointer;
  background-image: none;
  border: 1px solid transparent;
 
  border-radius: 4px;
 
  khtml-user-select: none;
  webkit-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  -webkit-user-select: none;
          user-select: none;
 
  -moz-transition: background, 0.3s;
  -0-transition: background, 0.3s;
  webkit-transition: background, 0.3s;
  transition: background, 0.3s;
}

.kafe-btn-default{
  font-family: 'Varela Round', sans-serif;
  letter-spacing: 1px;
  color: #fff;
  border-width: 1px;
  border-style: solid;
  background-color: transparent;
  border-color: #fff;
  margin-top: 10px;
  padding: 13px 31px;
}

.kafe-btn-default-new{
  font-family: 'Varela Round', sans-serif;
  letter-spacing: 1px;
  color: #fff;
  border-width: 1px;
  border-style: solid;
  background-color: transparent;
  border-color: #fff;
  padding: 5px 10px;
}
.kafe-btn-default-mint{
  font-family: 'Varela Round', sans-serif;
  letter-spacing: 1px;
  color: rgb(255, 7, 7);
  border-width: 1px;
  border-style: solid;
  background-color: transparent;
  border-color: #000000;
  padding: 13px 31px;
}
