  background-color: #000000;
  color: #fff !important;
  text-decoration: none;
}

.kafe-btn-mint{
  font-family: 'Varela Round', sans-serif;
  font-weight: 700;
  font-size: 14px;

  
  padding: 13px 31px;
  color: #fff !important;
  background: #ff9900; 
  letter-spacing: 1px; 
}


.kafe-btn-mint-small{
  font-size: 12px;
  padding: 4px;
