  background: #ff9900; 
}

.kafe-btn-danger{
  font-family: 'Varela Round', sans-serif;
  font-weight: 700;
  font-size: 14px;

  
  padding: 13px 31px;
  color: #fff !important;
  background: #FA5050; 
  letter-spacing: 1px; 
}

.kafe-btn-danger-small{
  font-family: 'Varela Round', sans-serif;
