  background: #FA5050; 
  letter-spacing: 1px; 
}

.kafe-btn-default:hover, .kafe-btn-default-new:hover {
  background-color: #fff;
  color: #2C3E50 !important;
  text-decoration: none;
}
.kafe-btn-danger:hover, .kafe-btn-danger-small:hover {
  color: #fff !important;
  background: #ba3434; 
